export function getDefaultUserAgentKey() {
  return "x-ms-useragent";
}
export function getPlatformSpecificData() {
  var navigator = window.navigator;
  var osInfo = {
    key: "OS",
    value: (navigator.oscpu || navigator.platform).replace(" ", "")
  };
  return [osInfo];
}